@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main-screen{color:#fff;background-color:#010606;box-sizing:border-box;margin:0;padding:20px 0 0 50px;height:100vh;font-family:"Roboto", sans-serif;display:flex;flex-direction:column;justify-content:center;align-items:center}@media screen and (max-width: 350px){.main-screen .title{font-size:24px}}

.vp-container{width:80%;min-width:200px;max-width:1200px;padding:10px;border:1px solid #303030;box-shadow:5px 2px 2px #303030}.vp-container .video{position:relative;z-index:1}.vp-container .video:hover>.image{display:block}.vp-container .video .image{position:absolute;top:50%;left:50%;background:rgba(48,48,48,0.5);border-radius:50%;display:none;animation:2s ease-in-out slidein;z-index:2}.vp-container .video .image:active{animation:1s ease-in-out slidein}@keyframes slidein{from{transform:scale(0)}to{transform:scale(1)}}.vp-container .video .speed{position:absolute;right:0;bottom:0;background-color:black;opacity:0.8;padding:5px}.vp-container .video .speed .speed-title{border-bottom:1px solid #303030}.vp-container .video .speed div{font-size:12px;padding:5px}.vp-container .video .speed div:hover{background-color:#303030}.vp-container .video-controls{display:flex;justify-content:space-between;padding-top:5px}.vp-container .video-controls .controls-left,.vp-container .video-controls .controls-right{display:flex;align-items:center;cursor:pointer;margin:0}.vp-container .video-controls .controls-left div,.vp-container .video-controls .controls-right div{padding-right:5px}.vp-container .video-controls .controls-left .volume:hover+.volumeProgress,.vp-container .video-controls .controls-right .volume:hover+.volumeProgress{visibility:visible}.vp-container .video-controls .controls-left .volumeProgress,.vp-container .video-controls .controls-right .volumeProgress{width:30px;visibility:hidden}@media screen and (max-width: 350px){.vp-container .video .speed div{font-size:8px}}@media screen and (min-width: 900px) and (max-height: 600px){.vp-container{width:50%}}@media screen and (min-width: 1500px) and (max-height: 800px){.vp-container{width:40%}}

